import { Injectable } from '@angular/core';
import { fromEvent, merge, of } from 'rxjs';
import { debounceTime, take } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class IdleService {
  idle$;
  constructor() {
    const events = [
      'mousemove',
      'keydown',
      'DOMMouseScroll',
      'mousewheel',
      'mousedown',
      'touchstart',
      'touchmove',
      'scroll',
      'click',
    ];
    const events$ = events.map(event => fromEvent(document, event));
    this.idle$ = merge(of(undefined), ...events$).pipe(
      debounceTime(9 * 60 * 60 * 1000), // 9 hours
      take(1) // unsubscribe after
    );
  }

  onIdle(cb) {
    return this.idle$.subscribe(cb);
  }
}
