import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import version from '../../../environments/version';
import { AuthService } from '../../auth/auth.service';
import { IdleService } from '../../idle/idle.service';
import { LoadingService } from '../../loading/loading.service';
import { shellAnimation } from '../animations';

declare const require: (path: string) => any;

@Component({
  selector: 'app-shell',
  templateUrl: './shell.component.html',
  styleUrls: ['./shell.component.css'],
  animations: [shellAnimation],
  encapsulation: ViewEncapsulation.None,
})
export class ShellComponent implements OnInit, OnDestroy {
  version = version;
  account;
  loading$;
  private _ngDestroy$ = new Subject<void>();
  constructor(
    private auth: AuthService,
    private loadingService: LoadingService,
    private idleService: IdleService
  ) {}

  ngOnInit() {
    this.loading$ = this.loadingService
      .getLoading()
      .pipe(takeUntil(this._ngDestroy$));

    this.idleService.onIdle(async () => {
      alert('User has been logged out due to inactivity.');
      this.logout();
    });

    this.account = this.auth.getAccount();

    // this.router.events.subscribe((event: Event) => {
    //   switch (true) {
    //     case event instanceof NavigationStart:
    //       this.loading = true;
    //       break;
    //     case event instanceof NavigationEnd:
    //     case event instanceof NavigationCancel:
    //     case event instanceof NavigationError:
    //       this.loading = false;
    //       break;
    //     default:
    //       break;
    //   }
    // });
  }

  ngOnDestroy() {
    this._ngDestroy$.next();
    this._ngDestroy$.complete();
  }

  logout() {
    this.auth.logout();
    window.location.reload();
  }

  assets(image) {
    // return require(`./assets/${image}`);
    return require(`../../features/login/login/assets/${image}`);
  }

  getRouterOutletState(o) {
    return o.isActivated ? o.activatedRoute : '';
  }
}
